import { type FC } from 'react';
import { Card } from 'antd';
import { CopyOutlined } from '@ant-design/icons';
import copy from 'copy-to-clipboard';

import SyntaxHighlighter from 'react-syntax-highlighter/dist/esm/light';
import js from 'react-syntax-highlighter/dist/esm/languages/hljs/javascript';
import xml from 'react-syntax-highlighter/dist/esm/languages/hljs/xml';
import bash from 'react-syntax-highlighter/dist/esm/languages/hljs/bash';
import dark from 'react-syntax-highlighter/dist/esm/styles/hljs/ir-black';

import './index.css';

SyntaxHighlighter.registerLanguage('javascript', js);
SyntaxHighlighter.registerLanguage('xml', xml);
SyntaxHighlighter.registerLanguage('bash', bash);

type CodeSnippetProps = {
  code: string;
  showLineNumbers?: boolean;
  language?: 'shell' | 'powershell' | 'javascript' | 'xml' | 'bash';
};

export const CodeSnippet: FC<CodeSnippetProps> = ({
  code,
  showLineNumbers = false,
  language = 'shell',
}) => {
  return (
    <Card
      type="inner"
      title={language}
      size="small"
      className="code-snippet"
      extra={
        <CopyOutlined style={{ color: '#fff' }} onClick={() => copy(code)} />
      }
    >
      <SyntaxHighlighter
        language={language}
        style={dark}
        customStyle={{ margin: 0 }}
        showLineNumbers={showLineNumbers}
        wrapLongLines
      >
        {code}
      </SyntaxHighlighter>
    </Card>
  );
};
